import React, { useEffect } from 'react';
import './UserObject.css';

export interface userProperties {
    name: string,
    img: string,
    points: number
}


function UserObject({ name, img, points }: userProperties) {

    useEffect(() => {

    }, [])

    return (
        <>
            <div className="User">
                <div className="content">
                    <img src={img} alt="" className="profile-picture" />
                    <div className="text-content">
                        <div className="name-text" id="name">{name}</div>
                        {points} Punkte
                    </div>
                </div>
            </div>
        </>
    );
}

export default UserObject;
