import React, { useEffect } from 'react';
import { userProperties } from '../UserList/UserObject';
import './UserList.css';
import UserObject from './UserObject';



function UserList({ users }: { users: Array<userProperties> }) {

    useEffect(() => {

    }, [])

    return (
        <>
            <div className="UserList">
                {
                    users.map((user) => {
                        return (<UserObject name={user.name} points={user.points} img={user.img} />)
                    })
                }
            </div>
        </>
    );
}

export default UserList;